@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins&display=swap");
/** Media Queries **/
.header {
  background-color: #eecc99;
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
}
@media (min-width: 992px) {
  .navbar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .logo {
    max-width: 30rem;
  }
}

.nav {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.3s linear;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 2rem;
    opacity: 1;
    visibility: visible;
  }
}
.nav a {
  font-weight: bold;
  text-transform: uppercase;
  color: black;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  text-align: center;
}
.nav a:hover {
  color: #dfa142;
}

@media (min-width: 768px) {
  .movil-menu {
    display: none;
  }
}
.movil-menu img {
  cursor: pointer;
  width: 5rem;
  filter: invert(1);
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.btn-submit {
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .btn-submit {
    margin: 0;
  }
}

.nav-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .nav-menu {
    flex-direction: row;
    gap: 3rem;
  }
}
.nav-menu .search {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
}
@media (min-width: 768px) {
  .nav-menu .search {
    margin: 0;
    flex-direction: row;
    gap: 0.3rem;
  }
}

.footer {
  padding: 2rem;
  background-color: #545871;
  margin: 10rem 0 0 0;
}
.footer .copyright {
  color: white;
  text-align: center;
}

.form-container {
  display: grid;
  grid-template-rows: 20% 80%;
  height: auto;
  margin: 5rem 0;
  border: 1px solid black;
  border-radius: 2rem;
  background-color: #545871;
  -webkit-box-shadow: 0px 8px 5px 0px rgba(0, 0, 0, 0.44);
  -moz-box-shadow: 0px 8px 5px 0px rgba(0, 0, 0, 0.44);
  box-shadow: 0px 8px 5px 0px rgba(0, 0, 0, 0.44);
}
@media (min-width: 768px) {
  .form-container {
    grid-template-columns: 1fr 2fr;
    grid-template-rows: auto;
    height: 50rem;
  }
}

.img-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 2rem;
}
.img-form img {
  height: 80%;
  width: 40%;
}
@media (min-width: 768px) {
  .img-form {
    justify-content: flex-end;
  }
  .img-form img {
    height: auto;
    width: 100%;
  }
}

.contenido-form {
  background-color: #eecc99;
  border-radius: 0 0 2rem 2rem;
  display: grid;
  grid-template-rows: 80% 20%;
}
@media (min-width: 768px) {
  .contenido-form {
    border-radius: 0 2rem 2rem 0;
  }
}
.contenido-form .footer-form {
  background-color: white;
  border-radius: 0 0 2rem 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .contenido-form .footer-form {
    border-bottom-left-radius: 0;
  }
}

.campos {
  margin: auto;
  border: none;
  max-width: 90%;
  height: 50rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .campos {
    height: auto;
    margin: auto;
  }
}
.campos label {
  font-size: 1.8rem;
}
.campos input:not([type=submit]) {
  display: block;
  padding: 1rem;
  width: 100%;
  margin: 1rem 0;
}
.campos .titulo {
  text-transform: uppercase;
  text-align: center;
}

.btn-submit {
  background-color: #0e2267;
  color: white;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  border-radius: 0.5rem;
}
.btn-submit:hover {
  background-color: #0b1b51;
  cursor: pointer;
}

input::-webkit-file-upload-button {
  background-color: #0e2267;
  color: white;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  border-radius: 0.5rem;
}
input::-webkit-file-upload-button:hover {
  background-color: #0b1b51;
  cursor: pointer;
}
@media (min-width: 768px) {
  input::-webkit-file-upload-button {
    background-color: #0e2267;
    color: white;
    font-weight: bold;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center;
    display: inline;
    border: none;
    border-radius: 0.5rem;
  }
  input::-webkit-file-upload-button:hover {
    background-color: #0b1b51;
    cursor: pointer;
  }
}

